import { useWeb3React } from "@web3-react/core";
import React, { useContext, useEffect, useState } from "react";
import styled, { ThemeContext } from "styled-components"
import { AddressBalanceInfo } from "../../dtos/AddressBalanceInfo";
import { TokenService } from "../../services/TokenService";
import { getEtherscanLink, shortenAddress } from "../../utils";
import { ExternalLink } from "../Link";
import Copy from '../AccountDetails/Copy'
import ContentLoader from "react-content-loader";
import { LinkStyledButton } from "../Button";
import { RefreshCw } from 'react-feather'
import { TokenBalanceInfo } from "../../dtos/TokenBalanceInfo";
import { PoolInfo } from "../../dtos/PoolInfo";
import { TokenInfo } from "../../dtos/TokenInfo";
import { KethToken, WethToken, WETH_ROOT_POOL_ADDRESS, KETH_ROOT_POOL_ADDRESS, TWO_POOL_CALCULATOR_ADDRESS } from "../../constants";
import { TwoPoolCalculatorService } from "../../services/TwoPoolCalculatorService";
import { CalculatorInfo } from "../../dtos/CalculatorInfo";

const Wrapper = styled.div`
    display: grid;
    grid-gap: 1em;
    height: 100%;
    width: 100%;
    padding: 1em 0;
` 

const SectionWrapper = styled.div`
    display: grid;
    grid-gap: 0.35em;

    :not(:last-child){
        border-bottom: 1px solid ${({ theme }) => theme.text5};  
        padding-bottom: 1em;
      }
`

const AddressLink = styled(ExternalLink)`
  font-size: 0.825rem;
  font-family: monospace;
  color: ${({ theme }) => theme.text3};
  margin-left: 1rem;
  display: flex;
  :hover {
    color: ${({ theme }) => theme.text2};
  } 
`

const AddressNameLink = styled(ExternalLink)`
  font-size: 0.825rem;
  color: ${({ theme }) => theme.text3};
  display: flex;
  :hover {
    color: ${({ theme }) => theme.text2};
  } 
`

const HeaderWrapper = styled.div`
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: auto auto auto 1fr;
    grid-gap: 0.5em;
    align-items:center;
    padding-bottom: 1em;
`

const BalancesWrapper = styled.div`
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 1fr 5em;
    grid-column-gap: 1em;
    justify-items: end;
    align-items: center;
    padding: 0 0.5em;
    font-size: 0.825rem;
    font-family: monospace;
`

const BalancesWrapperWithCaption = styled(BalancesWrapper)`
grid-template-columns: 1fr 6em 5em;
`

const Ticker = styled.div`
    justify-self: start;
`

const BalanceLoader = () => (
    <ContentLoader
    height={12}
    speed={1}
    animate={true}
    backgroundColor="#f6f6ef"
    foregroundColor="#e8e8e3"
    backgroundOpacity={0.06}
    foregroundOpacity={0.12}
    viewBox="0 0 80 16"
  >
    <rect x="0" y="4" rx="2" ry="2" width="80" height="12" />
  </ContentLoader>)

const RefreshButton = styled(LinkStyledButton)`
  color: ${({ theme }) => theme.text3};
  text-decoration: none;
  font-size: 0.875rem;
  :hover
  {
    text-decoration: none;
    color: ${({ theme }) => theme.text2};
  }
`

const RefreshIcon = styled.span`
  margin-right: 0.25rem;
  font-size: 0.875rem;
  ${({ theme }) => theme.flexRowNoWrap};
  align-items: center;
`

const Error = styled.div`
    color: ${({ theme }) => theme.red1};
    justify-self:start;
`;

const SectionHeader = ({name, address, onRefresh} : {name: string, address:string, onRefresh: () => void })=> {
    const { chainId } = useWeb3React()
    const theme = useContext(ThemeContext)

    return (
      <HeaderWrapper> 
        <AddressNameLink href={getEtherscanLink(chainId!, address, 'address')}>
            <span style={{ marginLeft: '4px' }}>{name}</span>
        </AddressNameLink>       
        <AddressLink href={getEtherscanLink(chainId!, address, 'address')}>
            <span style={{ marginLeft: '4px' }}>{shortenAddress(address)}</span>
        </AddressLink>
        <Copy toCopy={address} color={theme.text3}/>
        <RefreshButton onClick={onRefresh}>
            <RefreshIcon>
                <RefreshCw size={'16'}/>
            </RefreshIcon>
        </RefreshButton>        
    </HeaderWrapper>
    )
}

const AddressBalances = ({info, globalLoading}:{info: AddressBalanceInfo, globalLoading: boolean}) => {
    const { account, library, chainId } = useWeb3React()
    const [loading, setLoading] = useState<boolean>(false)
    const [addressBalances, setAddressBalances] = useState<TokenBalanceInfo[]>([]) 

    useEffect(() => {
        setAddressBalances(info.balances)
    }, [info.balances])

    const refreshBalance = async () => {        
        if (library && account && chainId) {
            const service = new TokenService(library, account!)
            setLoading(true)           
            setAddressBalances(await service.getAddressBalances(info.address))
            setLoading(false)
        }
    }

    return (
    <SectionWrapper>       
        <SectionHeader name={info.name} address={info.address} onRefresh={refreshBalance} /> 
        {addressBalances?.map(x=>(
            <BalancesWrapper key={x.token.symbol}>
                <div>
                    {loading || globalLoading 
                    ? <BalanceLoader/> 
                    : x.balance}
                </div>
                <Ticker>{x.token.symbol}</Ticker> 
            </BalancesWrapper>))}        
    </SectionWrapper>)
}

const PoolBalances = ({token, poolAddress} : {token: TokenInfo, poolAddress: string}) => {
    const { account, library, chainId } = useWeb3React()
    const [loading, setLoading] = useState<boolean>(false)
    const [poolInfo, setPoolInfo] = useState<PoolInfo>()
    
    const getPoolInfo = async () =>{
        if (library && account && chainId) {      
            const service = new TokenService(library, account!)      
            setLoading(true)           
            setPoolInfo(await service.getPoolInfo(poolAddress, token.address))
            setLoading(false)
        }
    }

    useEffect(() => {           
        const getPoolInfo = async () => {           
            if (library && account && chainId) {  
                const service = new TokenService(library, account!)              
                setLoading(true)           
                setPoolInfo(await service.getPoolInfo(poolAddress, token.address))
                setLoading(false)
            }
        }       
        getPoolInfo()        
    }, [library, account, chainId, poolAddress, token])

    return (
        <SectionWrapper>
             <SectionHeader name={`${token.symbol} / ROOT Pool`} address={poolAddress} onRefresh={getPoolInfo} /> 
             <BalancesWrapper>
                <div>{loading ? <BalanceLoader/> : poolInfo?.tokenBalance}</div>
                <Ticker>{token.symbol}</Ticker>
            </BalancesWrapper>
            <BalancesWrapper>
                <div>{loading ? <BalanceLoader/> : poolInfo?.rootBalance}</div>
                <Ticker>ROOT</Ticker>
            </BalancesWrapper>
            <BalancesWrapper>
                <div>{loading ? <BalanceLoader/> : poolInfo?.lpBalance}</div>
                <Ticker>LP</Ticker>
            </BalancesWrapper>
            <BalancesWrapper>                
                <div>1 ROOT = {loading ? <BalanceLoader/> : poolInfo?.rootPrice}</div>
                <Ticker>{token.symbol}</Ticker> 
            </BalancesWrapper>
        </SectionWrapper>
    )
}

const TwoPoolCalculatorSubFloor = () => {
    const { account, library, chainId } = useWeb3React()
    const [loading, setLoading] = useState<boolean>(false)
    const [calculatorInfo, setCalculatorInfo] = useState<CalculatorInfo>()

    const calculateSubfloor = async () =>{
        if (library && account && chainId) {      
            const service = new TwoPoolCalculatorService(library, account!)      
            setLoading(true)           
            setCalculatorInfo(await service.calculatorSubFloorAndExcess())
            setLoading(false)
        }
    }

    useEffect(() => {           
        const calculateSubfloor = async () => {           
            if (library && account && chainId) {  
                const service = new TwoPoolCalculatorService(library, account!)      
                setLoading(true)           
                setCalculatorInfo(await service.calculatorSubFloorAndExcess())
                setLoading(false)
            }
        }       
        calculateSubfloor()
    }, [library, account, chainId])

    return (
        <SectionWrapper>
            <SectionHeader name={"Two Pool Calculator"} address={TWO_POOL_CALCULATOR_ADDRESS} onRefresh={calculateSubfloor} /> 
            <BalancesWrapperWithCaption>
                <div>Sub Floor</div>
                {loading ? <BalanceLoader/> : calculatorInfo?.subFloor === "ERROR" ? (<Error>MISSING</Error>) : <div>{calculatorInfo?.subFloor}</div>}
                <Ticker>{!loading && calculatorInfo?.subFloor === "ERROR" ? "" : "WETH"}</Ticker>                
            </BalancesWrapperWithCaption>
            <BalancesWrapperWithCaption>
                <div>Excess</div>
                <div>{loading ? <BalanceLoader/> : calculatorInfo?.excess === "ERROR" ? "" : calculatorInfo?.excess}</div>
                <Ticker>{!loading && calculatorInfo?.excess === "ERROR" ? (<Error>ERROR</Error>) : "WETH"}</Ticker> 
            </BalancesWrapperWithCaption>
        </SectionWrapper>
    )
}

const Balances = () => {
    const { account, library, chainId } = useWeb3React()
    const [balances, setBalances] = useState<AddressBalanceInfo[]>()
    const [loading, setLoading] = useState<boolean>(true)

    useEffect(() => {       

        const getBalances = async () => {
            if (library && account && chainId){
                const service = new TokenService(library, account!)
                setLoading(true)           
                setBalances(await service.getAllBalances())
                setLoading(false)
            }
        }

        if(library && account){
            const service = new TokenService(library, account!)
            setBalances(service.getEmptyBalances())
            getBalances()
        }
    }, [library, account, chainId])

    return (
    <Wrapper>
        {balances?.map(x=>(<AddressBalances key={x.address} globalLoading={loading} info={x}/>)) }
        <PoolBalances token={WethToken} poolAddress={WETH_ROOT_POOL_ADDRESS} />
        <PoolBalances token={KethToken} poolAddress={KETH_ROOT_POOL_ADDRESS} /> 
        <TwoPoolCalculatorSubFloor />
    </Wrapper>)
}

export default Balances;