import { useWeb3React } from "@web3-react/core"
import React from "react"
import styled from "styled-components"
import Balances from "../../components/Balances"
import Bobber from "../../components/Bobber"
import Deployer from "../../components/Deployer"

const Wrapper = styled.div`
    display: grid;
    grid-template-areas: "bobber balances" "deployer balances";
    grid-template-columns: 2.5fr 1fr;
    padding: 1em;
    grid-gap: 1em;
    width:100%;

   ${({ theme }) => theme.mediaWidth.upToMedium`
        grid-template-areas: "bobber" "deployer" "balances";
        grid-template-columns: auto;
   `};
`

const Section = styled.div`
    background: ${({ theme }) => theme.bg1};
    box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 8px 12px rgba(0, 0, 0, 0.04),
    0px 12px 16px rgba(0, 0, 0, 0.01);
    border-radius: 1.25em;
    color: ${({ theme }) => theme.text2};
    padding: 1em;
`

const SectionHeader = styled.div`
    padding: 0 0.25em 0.15em 0.1em;
    color: ${({ theme }) => theme.text3};
    border-bottom: 1px solid ${({ theme }) => theme.text3};
    text-transform: uppercase;
`

const BobberSection = styled(Section)`
    grid-area: bobber;
`

const DeployerSection = styled(Section)`
    grid-area: deployer;
`

const BalancesSection = styled(Section)`
    grid-area: balances;
`

export const Home = () => {
    const { account, library, chainId } = useWeb3React()

    return (
        account && library && chainId
            ?
            (
                <Wrapper>
                    <BobberSection>
                        <SectionHeader>Bobber</SectionHeader>
                        <Bobber />
                    </BobberSection>
                    <DeployerSection>
                        <SectionHeader>Deployer</SectionHeader>
                        <Deployer/>
                    </DeployerSection>
                    <BalancesSection>
                        <SectionHeader>Balances</SectionHeader>
                        <Balances />
                    </BalancesSection>
                </Wrapper>
            )
            : null
    )
}