import React, { useEffect, useState } from "react"
import CurrencyInput from "../CurrencyInput"
import { TokenService } from "../../services/TokenService";
import { useWeb3React } from "@web3-react/core"
import { KETH_ROOT_POOL_ADDRESS, PONZO_MA_BOBBER_ADDRESS } from "../../constants"
import { BobberService } from "../../services/BobberService"
import ActionModal from "../ActionModal"

const ZapKethToWeth = ({ isOpen, onDismiss }: { isOpen: boolean, onDismiss: () => void }) => {
    const { account, library } = useWeb3React()
    const [value, setValue] = useState<string>("")
    const [balance, setBalance] = useState<number>(0)

    useEffect(() => {
        const getKethBalance = async () => setBalance(await tokenService.getBalance(PONZO_MA_BOBBER_ADDRESS, KETH_ROOT_POOL_ADDRESS))
        const tokenService = new TokenService(library, account!)
        getKethBalance()
    })

    const zapKethToWeth = async () => {
        const amount = parseFloat(value)
        if (!Number.isNaN(amount) && amount > 0) {
            return await new BobberService(library, account!).zapKethToWeth(value)
        }
    }

    const close = () => {
        setValue("")
        onDismiss()
    }

    return (
        <ActionModal isOpen={isOpen} onDismiss={close} action={zapKethToWeth} title={"Zap KETH to WETH"}>
            <CurrencyInput
                value={value}
                balance={balance}
                onSubmit={zapKethToWeth}
                ticker={"LP"}
                label={"Liquidity to remove"}
                onMax={() => setValue(balance.toString())}
                showMaxButton={true}
                onUserInput={(x) => setValue(x)}
                id={"zapKethToWethInput"} />
        </ActionModal>
    )
}

export default ZapKethToWeth