import React, { useEffect, useState } from "react"
import CurrencyInput from "../CurrencyInput"
import { TokenService } from "../../services/TokenService";
import { useWeb3React } from "@web3-react/core"
import { getTokenByAddress, PONZO_MA_BOBBER_ADDRESS, ROOT_ADDRESS } from "../../constants"
import { BobberService } from "../../services/BobberService"
import ActionModal from "../ActionModal"

const AddLiquidity = ({ tokenAddress, isOpen, onDismiss } : { tokenAddress: string, isOpen: boolean, onDismiss: () => void }) => {
    const { account, library } = useWeb3React()
    const [value, setValue] = useState<string>("")
    const [balance, setBalance] = useState<number>(0)
    const token = getTokenByAddress(tokenAddress)

    useEffect(() => {
        const getRootBalance = async () => setBalance(await tokenService.getBalance(PONZO_MA_BOBBER_ADDRESS, ROOT_ADDRESS))
        const tokenService = new TokenService(library, account!)
        getRootBalance()
    })

    const addLiquidity = async () => {
        const amount = parseFloat(value)
        if (!Number.isNaN(amount) && amount > 0) {
            return await new BobberService(library, account!).addLiquidity(tokenAddress, value)
        }
    }

    const close = () => {
        setValue("")
        onDismiss()
    }

    return (
        <ActionModal isOpen={isOpen} onDismiss={close} action={addLiquidity} title={`Add ${token?.symbol} liquidity`}>
            <CurrencyInput
                value={value}
                balance={balance}
                onSubmit={addLiquidity}
                ticker={"ROOT"}
                label={"Amount to add"}
                onMax={() => setValue(balance.toString())}
                showMaxButton={true}
                onUserInput={(x) => setValue(x)}
                id={"addLiquidityInput"} />
        </ActionModal>
    )
}

export default AddLiquidity