import React, { useState } from "react"
import styled from "styled-components"
import { ButtonPrimary, ButtonPrimaryRed, ButtonPrimaryGreen } from "../Button"
import UnwrapKeth from "./UnwrapKeth"
import ZapKethToWeth from "./ZapKethToWeth"
import AddLiquidity from "./AddLiquidity"
import { KETH_ADDRESS, KETH_ROOT_POOL_ADDRESS, ROOT_ADDRESS, WETH_ADDRESS, WETH_ROOT_POOL_ADDRESS } from "../../constants"
import RemoveLiquidity from "./RemoveLiquidity"
import BuyRoot from "./BuyRoot"
import SellRoot from "./SellRoot"
import WrapToKeth from "./WrapToKeth"
import ZapWethToKeth from "./ZapWethToKeth"
import SetInfinitePumper from "./SetInfinitePumper"
import PumpItPonzo from "./PumpItPonzo"
import RecoverTokens from "./RecoverTokens"
import SetAndSweep from "./SetAndSweep"
import IveAbandonedEverythingIOnceStoodFor from "./IveAbandonedEverythingIOnceStoodFor"

const Wrapper = styled.div`
    display: grid;
    grid-gap: 1em;
    width: 100%;
    padding: 1em 0 0 0;
`

const SectionWrapper = styled.div`
:not(:last-child){
  border-bottom: 1px solid ${({ theme }) => theme.text5};  
  padding-bottom: 1em;
}
`

const SectionContent = styled.div`
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 1em;
`

const SectionHeader = styled.div`
  padding-left: 0.25em;
  padding-bottom: 1em;  
  color: ${({ theme }) => theme.text3};
`

const Bobber = () => {
  const [unwrapKethOpen, setUnwrapKethOpen] = useState<boolean>(false)
  const [wrapToKethOpen, setWrapToKethOpen] = useState<boolean>(false)
  const [zapKethToWethOpen, setZapKethToWethOpen] = useState<boolean>(false)
  const [zapWethToKethOpen, setZapWethToKethOpen] = useState<boolean>(false)
  const [addKethLiquidityOpen, setAddKethLiquidityOpen] = useState<boolean>(false)
  const [removeKethLiquidityOpen, setRemoveKethLiquidityOpen] = useState<boolean>(false)
  const [buyRootWithKethOpen, setBuyRootWithKethOpen] = useState<boolean>(false)
  const [sellRootForKethOpen, setSellRootForKethOpen] = useState<boolean>(false)
  const [addWethLiquidityOpen, setAddWethLiquidityOpen] = useState<boolean>(false)
  const [removeWethLiquidityOpen, setRemoveWethLiquidityOpen] = useState<boolean>(false)
  const [buyRootWithWethOpen, setBuyRootWithWethOpen] = useState<boolean>(false)
  const [sellRootForWethOpen, setSellRootForWethOpen] = useState<boolean>(false)

  const [setAndSweepOpen, setSetAndSweepOpen] = useState<boolean>(false)
  const [iveAbandonedEverythingIOnceStoodForOpen, setIveAbandonedEverythingIOnceStoodForOpen] = useState<boolean>(false)
  const [infinitePumperOpen, setInfinitePumperOpen] = useState<boolean>(false)
  const [pumpItPonzoWethOpen, setPumpItPonzoWethOpen] = useState<boolean>(false)
  const [pumpItPonzoKethOpen, setPumpItPonzoKethOpen] = useState<boolean>(false)
  const [recoverTokensOpen, setRecoverTokenOpen] = useState<boolean>(false)
  const [recoverTokensAddress, setRecoverTokensAddress] = useState<string>(WETH_ADDRESS)

  const recoverTokens = (address: string) => {
    setRecoverTokensAddress(address)
    setRecoverTokenOpen(true)
  }

  return (
    <Wrapper>
      
      <UnwrapKeth isOpen={unwrapKethOpen} onDismiss={() => setUnwrapKethOpen(false)} />
      <ZapKethToWeth isOpen={zapKethToWethOpen} onDismiss={() => setZapKethToWethOpen(false)} />
      <WrapToKeth isOpen={wrapToKethOpen} onDismiss={() => setWrapToKethOpen(false)} />
      <ZapWethToKeth isOpen={zapWethToKethOpen} onDismiss={() => setZapWethToKethOpen(false)} />
      <AddLiquidity tokenAddress={KETH_ADDRESS} isOpen={addKethLiquidityOpen} onDismiss={() => setAddKethLiquidityOpen(false)} />
      <RemoveLiquidity tokenAddress={KETH_ADDRESS} lpAddress={KETH_ROOT_POOL_ADDRESS} isOpen={removeKethLiquidityOpen} onDismiss={() => setRemoveKethLiquidityOpen(false)} />
      <BuyRoot tokenAddress={KETH_ADDRESS} isOpen={buyRootWithKethOpen} onDismiss={() => setBuyRootWithKethOpen(false)} />
      <SellRoot tokenAddress={KETH_ADDRESS} isOpen={sellRootForKethOpen} onDismiss={() => setSellRootForKethOpen(false)} />
      <AddLiquidity tokenAddress={WETH_ADDRESS} isOpen={addWethLiquidityOpen} onDismiss={() => setAddWethLiquidityOpen(false)} />
      <RemoveLiquidity tokenAddress={WETH_ADDRESS} lpAddress={WETH_ROOT_POOL_ADDRESS} isOpen={removeWethLiquidityOpen} onDismiss={() => setRemoveWethLiquidityOpen(false)} />
      <BuyRoot tokenAddress={WETH_ADDRESS} isOpen={buyRootWithWethOpen} onDismiss={() => setBuyRootWithWethOpen(false)} />
      <SellRoot tokenAddress={WETH_ADDRESS} isOpen={sellRootForWethOpen} onDismiss={() => setSellRootForWethOpen(false)} />

   
      <SetInfinitePumper isOpen={infinitePumperOpen} onDismiss={() => setInfinitePumperOpen(false)} />
      <PumpItPonzo tokenAddress={WETH_ADDRESS} lpAddress={WETH_ROOT_POOL_ADDRESS} isOpen={pumpItPonzoWethOpen} onDismiss={() => setPumpItPonzoWethOpen(false)} />
      <PumpItPonzo tokenAddress={KETH_ADDRESS} lpAddress={KETH_ROOT_POOL_ADDRESS} isOpen={pumpItPonzoKethOpen} onDismiss={() => setPumpItPonzoKethOpen(false)} />
      <SetAndSweep isOpen={setAndSweepOpen} onDismiss={() => setSetAndSweepOpen(false)} />
      <IveAbandonedEverythingIOnceStoodFor isOpen={iveAbandonedEverythingIOnceStoodForOpen} onDismiss={() => setIveAbandonedEverythingIOnceStoodForOpen(false)} />
      <RecoverTokens tokenAddress={recoverTokensAddress} isOpen={recoverTokensOpen} onDismiss={() => setRecoverTokenOpen(false)} />

      <SectionWrapper>
        <SectionHeader>Ponzee</SectionHeader>
        <SectionContent>          
          <ButtonPrimary onClick={() => setPumpItPonzoKethOpen(true)}>Pump It Ponzo KETH</ButtonPrimary>
          <ButtonPrimary onClick={() => setPumpItPonzoWethOpen(true)}>Pump It Ponzo WETH</ButtonPrimary>
          <ButtonPrimary onClick={() => setIveAbandonedEverythingIOnceStoodForOpen(true)}>I've Abandoned Everything I Once Stood For</ButtonPrimary>
          <ButtonPrimary onClick={() => setSetAndSweepOpen(true)}>Set and Sweep</ButtonPrimary>
          <ButtonPrimary onClick={() => setInfinitePumperOpen(true)}>Set Infinite Pumper</ButtonPrimary>
        </SectionContent>
      </SectionWrapper>
      <SectionWrapper>

        <SectionHeader>KETH</SectionHeader>
        <SectionContent>
          <ButtonPrimary onClick={() => setUnwrapKethOpen(true)}>Unwrap KETH</ButtonPrimary>
          <ButtonPrimary onClick={() => setZapKethToWethOpen(true)}>Zap KETH to WETH</ButtonPrimary>
          <ButtonPrimaryGreen onClick={() => setAddKethLiquidityOpen(true)}>Add KETH Liquidity</ButtonPrimaryGreen>
          <ButtonPrimaryRed onClick={() => setRemoveKethLiquidityOpen(true)}>Remove KETH Liquidity</ButtonPrimaryRed>
          <ButtonPrimaryGreen onClick={() => setBuyRootWithKethOpen(true)}>Buy ROOT with KETH</ButtonPrimaryGreen>
          <ButtonPrimaryRed onClick={() => setSellRootForKethOpen(true)}>Sell ROOT for KETH</ButtonPrimaryRed>
        </SectionContent>
      </SectionWrapper>

      <SectionWrapper>
        <SectionHeader>WETH</SectionHeader>
        <SectionContent>
          <ButtonPrimary onClick={() => setWrapToKethOpen(true)}>Wrap to KETH</ButtonPrimary>
          <ButtonPrimary onClick={() => setZapWethToKethOpen(true)}>Zap WETH to KETH</ButtonPrimary>
          <ButtonPrimaryGreen onClick={() => setAddWethLiquidityOpen(true)}>Add WETH Liquidity</ButtonPrimaryGreen>
          <ButtonPrimaryRed onClick={() => setRemoveWethLiquidityOpen(true)}>Remove WETH Liquidity</ButtonPrimaryRed>
          <ButtonPrimaryGreen onClick={() => setBuyRootWithWethOpen(true)}>Buy ROOT with WETH</ButtonPrimaryGreen>
          <ButtonPrimaryRed onClick={() => setSellRootForWethOpen(true)}>Sell ROOT for WETH</ButtonPrimaryRed>
        </SectionContent>
      </SectionWrapper>

      <SectionWrapper>
        <SectionHeader>Recover</SectionHeader>
        <SectionContent>
          <ButtonPrimary onClick={() => recoverTokens(WETH_ADDRESS)}>WETH</ButtonPrimary>
          <ButtonPrimary onClick={() => recoverTokens(KETH_ADDRESS)}>KETH</ButtonPrimary>
          <ButtonPrimary onClick={() => recoverTokens(ROOT_ADDRESS)}>ROOT</ButtonPrimary>
          <ButtonPrimary onClick={() => recoverTokens(WETH_ROOT_POOL_ADDRESS)}>WETH LP</ButtonPrimary>
          <ButtonPrimary onClick={() => recoverTokens(KETH_ROOT_POOL_ADDRESS)}>KETH LP</ButtonPrimary>
        </SectionContent>
      </SectionWrapper>
    </Wrapper>)
}

export default Bobber;