import React, { useState } from "react"
import { useWeb3React } from "@web3-react/core"
import ActionModal from "../ActionModal"
import { BobberService}  from "../../services/BobberService";
import NumberInput from "../NumberInput";

const IveAbandonedEverythingIOnceStoodFor = ({ isOpen, onDismiss } : { isOpen: boolean, onDismiss: () => void }) => {
    const { account, library } = useWeb3React()
    const [value, setValue] = useState<string>("")

    const iveAbandonedEverythingIOnceStoodFor = async () => {      
        const amount = parseFloat(value)
        if (!Number.isNaN(amount) && amount > 0) {
            return await new BobberService(library, account!).iveAbandonedEverythingIOnceStoodFor(value)
        }        
    }

    const close = () => {
        setValue("")
        onDismiss()
    }

    return (
        <ActionModal isOpen={isOpen} onDismiss={close} action={iveAbandonedEverythingIOnceStoodFor} title={"I've abandoned everything I once stood for"}>
            <NumberInput 
                label="loops" 
                value={value}
                onSubmit={iveAbandonedEverythingIOnceStoodFor}
                onUserInput={(x) => setValue(x)} 
                id={"loopsInput"} />
        </ActionModal>
    )
}

export default IveAbandonedEverythingIOnceStoodFor