import React, { useState } from "react"
import { useWeb3React } from "@web3-react/core"
import { BobberService } from "../../services/BobberService"
import ActionModal from "../ActionModal"
import { isAddress } from "../../utils";
import AddressInput from "../AddressInput"
import Toggle from "../Toggle";
import styled from "styled-components";

const ToggleWrapper = styled.div`
    display: grid;
    grid-template-columns: auto 1fr;
    grid-gap: 1em;  
    align-items: center;  
`

const ToggleLabel = styled.span`
    color: ${({ theme }) => theme.text3};
    font-size: 0.875em;
    padding-left: 1em;
`

const SetInfinitePumper = ({ isOpen, onDismiss } : { isOpen: boolean, onDismiss: () => void }) => {
    const { account, library } = useWeb3React()
    const [address, setAddress] = useState<string>("")
    const [infinite, setInfinite] = useState<boolean>(true)

    const setInfinitePumper = async () => {        
        if (address && isAddress(address)) {
            return await new BobberService(library, account!).setInfinitePumper(address, infinite)
        }
    }

    const close = () => {
        setAddress("")
        onDismiss()
    }

    return (
        <ActionModal isOpen={isOpen} onDismiss={close} action={setInfinitePumper} title={"Set Infinite Pumper"}>
           <AddressInput value={address} label={"Pumper"} onChange={setAddress} />
           <ToggleWrapper>
               <ToggleLabel>Infinite</ToggleLabel>
               <Toggle isActive={infinite} toggle={() => setInfinite(!infinite)}/>
           </ToggleWrapper>          
        </ActionModal>
    )
}

export default SetInfinitePumper