import { Contract } from '@ethersproject/contracts'
import twoPoolCalculatorAbi from '../constants/abis/twoPoolCalculator.json'
import { Web3Provider } from '@ethersproject/providers'
import { KETH_ADDRESS, TWO_POOL_CALCULATOR_ADDRESS, WETH_ADDRESS } from '../constants';
import { getDisplayBalance } from '../utils/formatBalance';
import BigNumber from 'bignumber.js';
import { CalculatorInfo } from '../dtos/CalculatorInfo';

export class TwoPoolCalculatorService {
    private contract: Contract

    constructor(library: Web3Provider, account: string){
        const signer = library.getSigner(account).connectUnchecked()
        this.contract = new Contract(TWO_POOL_CALCULATOR_ADDRESS, twoPoolCalculatorAbi, signer)        
    }

    public async calculatorSubFloorAndExcess()
    {
        let twoPoolSubFloor = ""
        let excessInPools = "";
        try {
            const subFloor = await this.contract.calculateSubFloor(WETH_ADDRESS, KETH_ADDRESS)
            twoPoolSubFloor = getDisplayBalance(new BigNumber(subFloor.toString()))
        }
        catch(e) {
            twoPoolSubFloor = "ERROR"
        }

        try {
            const excess = await this.contract.calculateExcessInPools(WETH_ADDRESS, KETH_ADDRESS)
            excessInPools = getDisplayBalance(new BigNumber(excess.toString()))
        }
        catch(e) {
            excessInPools = "ERROR"
        }     

        return new CalculatorInfo(twoPoolSubFloor, excessInPools)
    }
}