import { Contract } from '@ethersproject/contracts'
import bobberAbi from '../constants/abis/bobber.json'
import { Web3Provider } from '@ethersproject/providers'
import { PONZO_MA_BOBBER_ADDRESS } from '../constants';
import { parseEther } from '@ethersproject/units'

export class BobberService {
    contract: Contract
    gasMultiplier: number = 1.1

    constructor(library: Web3Provider, account: string){
        const signer = library.getSigner(account).connectUnchecked()
        this.contract = new Contract(PONZO_MA_BOBBER_ADDRESS, bobberAbi, signer)  
    } 

    public async pumpItPonzo(lpAmount: string, tokenAddress: string) {
        const gas = await this.contract.estimateGas.pumpItPonzo(parseEther(lpAmount), tokenAddress)
        return await this.contract.pumpItPonzo(parseEther(lpAmount), tokenAddress, { gasLimit: gas.toNumber() * this.gasMultiplier })
    }

    public async setAndSweep() {
        const gas = await this.contract.estimateGas.setAndSweep()
        return await this.contract.setAndSweep({ gasLimit: gas.toNumber() * this.gasMultiplier })
    }
   
    public async setInfinitePumper(pumper: string, infinite: boolean) {
        return await this.contract.setInfinitePumper(pumper, infinite)
    }

    public async iveAbandonedEverythingIOnceStoodFor(loops: string){
        const gas = await this.contract.estimateGas.iveAbandonedEverythingIOnceStoodFor(loops)
        return await this.contract.iveAbandonedEverythingIOnceStoodFor(loops, { gasLimit: gas.toNumber() * this.gasMultiplier })
    }

    public async zapKethToWeth(lpTokenAmount: string) {
        return await this.contract.zapKethToWeth(parseEther(lpTokenAmount))
    }

    public async zapWethToKeth(lpTokenAmount: string) {
        return await this.contract.zapWethToKeth(parseEther(lpTokenAmount))
    }

    public async wrapToKeth(wethAmount: string) {        
        return await this.contract.wrapToKeth(parseEther(wethAmount))
    }

    public async unwrapKeth(kethAmount: string) {        
        return await this.contract.unwrapKeth(parseEther(kethAmount))
    }

    public async buyRoot(tokenAddress: string, amountToSpend: string) {
        return await this.contract.buyRootKit(tokenAddress, parseEther(amountToSpend))
    }

    public async sellRoot(tokenAddress: string, amountToSpend: string) {
        return await this.contract.sellRootKit(tokenAddress, parseEther(amountToSpend))
    }

    public async addLiquidity(tokenAddress: string, amountToAdd: string) {
        return await this.contract.addLiquidity(tokenAddress, parseEther(amountToAdd))
    }

    public async removeLiquidity(tokenAddress: string, amountToRemove: string) {
        return await this.contract.removeLiquidity(tokenAddress, parseEther(amountToRemove))
    }

    public async recoverTokens(tokenAddress: string) {
        return await this.contract.recoverTokens(tokenAddress)
    }
}  