import React, { useEffect, useState } from "react"
import CurrencyInput from "../CurrencyInput"
import { TokenService } from "../../services/TokenService";
import { useWeb3React } from "@web3-react/core"
import { getTokenByAddress, PONZO_MA_BOBBER_ADDRESS } from "../../constants"
import { BobberService } from "../../services/BobberService"
import ActionModal from "../ActionModal"

const BuyRoot = ({ tokenAddress, isOpen, onDismiss } : { tokenAddress: string, isOpen: boolean, onDismiss: () => void }) => {
    const { account, library } = useWeb3React()
    const [value, setValue] = useState<string>("")
    const [balance, setBalance] = useState<number>(0)
    const token = getTokenByAddress(tokenAddress)

    useEffect(() => {
        const getKethBalance = async () => setBalance(await tokenService.getBalance(PONZO_MA_BOBBER_ADDRESS, tokenAddress))
        const tokenService = new TokenService(library, account!)
        getKethBalance()
    })

    const buyRoot = async () => {
        const amount = parseFloat(value)
        if (!Number.isNaN(amount) && amount > 0) {
            return await new BobberService(library, account!).buyRoot(tokenAddress, value)
        }
    }

    const close = () => {
        setValue("")
        onDismiss()
    }

    return (
        <ActionModal isOpen={isOpen} onDismiss={close} action={buyRoot} title={`Buy ROOT with ${token?.symbol}`}>
            <CurrencyInput
                value={value}
                balance={balance}
                onSubmit={buyRoot}
                ticker={token!.symbol}
                label={"Amount to spend"}
                onMax={() => setValue(balance.toString())}
                showMaxButton={true}
                onUserInput={(x) => setValue(x)}
                id={"buyRootInput"} />
        </ActionModal>
    )
}

export default BuyRoot