import React from "react"
import { useWeb3React } from "@web3-react/core"
import ActionModal from "../ActionModal"
import { BobberService}  from "../../services/BobberService";
import styled from "styled-components";

const Text = styled.span`
    color: ${({ theme }) => theme.text3};
    font-size: 0.875em;
`

const SetAndSweep = ({ isOpen, onDismiss } : { isOpen: boolean, onDismiss: () => void }) => {
    const { account, library } = useWeb3React()

    const setAndSweep = async () => {        
        return await new BobberService(library, account!).setAndSweep()
    }

    const close = () => {
        onDismiss()
    }

    return (
        <ActionModal isOpen={isOpen} onDismiss={close} action={setAndSweep} title={"Set and Sweep"}>
            <Text>Are you sure?</Text>
        </ActionModal>
    )
}

export default SetAndSweep