import React, { useEffect, useState } from "react"
import { useWeb3React } from "@web3-react/core"
import ActionModal from "../ActionModal"
import { BobberService}  from "../../services/BobberService";
import { getTokenByAddress, PONZO_MA_BOBBER_ADDRESS } from "../../constants";
import { TokenService } from "../../services/TokenService";
import styled from "styled-components";

const Text = styled.span`
    color: ${({ theme }) => theme.text3};
    font-size: 0.875em;
`

const RecoverTokens = ({ tokenAddress, isOpen, onDismiss } : { tokenAddress: string, isOpen: boolean, onDismiss: () => void }) => {
    const { account, library } = useWeb3React()
    const [balance, setBalance] = useState<number>(0)
    const token = getTokenByAddress(tokenAddress)

    useEffect(() => {
        const getBalance = async () => setBalance(await tokenService.getBalance(PONZO_MA_BOBBER_ADDRESS, tokenAddress))
        const tokenService = new TokenService(library, account!)
        getBalance()
    })

    const recoverTokens = async () => {        
        return await new BobberService(library, account!).recoverTokens(tokenAddress)
    }

    const close = () => {
        setBalance(0)
        onDismiss()
    }

    return (
        <ActionModal isOpen={isOpen} onDismiss={close} action={recoverTokens} title={`Recover ${token?.symbol}`}>
            <Text>{`Recover ${balance} ${token?.symbol}`} </Text>
        </ActionModal>
    )
}

export default RecoverTokens